@import url("https://fonts.googleapis.com/css?family=Amatic+SC");
@import url("https://fonts.googleapis.com/css?family=Gaegu");
h1, h2, h3, h4, h5, p, a, span, select, input, textarea, button {
  font-family: Arial;
  margin: 0px;
  font-weight: 100; }
  h1.Amatic, h2.Amatic, h3.Amatic, h4.Amatic, h5.Amatic, p.Amatic, a.Amatic, span.Amatic, select.Amatic, input.Amatic, textarea.Amatic, button.Amatic {
    font-family: 'Amatic SC', Arial;
    font-weight: bold; }

.info {
  margin: 83px 50px 0 50px;
  text-align: center; }
  .info p {
    font-size: 16px;
    font-family: "Gaegu", Arial; }
    .info p:nth-of-type(2) {
      margin-top: 10px; }



