.hero {
  height: 35vh;
  background-size: cover;
  background-position: center center;
  position: relative; }
  .hero:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
  .hero.hair {
    background-image: url(./../assets/img/home/hair.jpg); }
    .hero.hair:before {
      background-color: rgba(255, 206, 26, 0.2); }
  .hero.beauty {
    background-image: url(../assets/img/home/beauty.jpg); }
    .hero.beauty:before {
      background-color: rgba(64, 255, 149, 0.2); }
  .hero.specialist {
    background-image: url(../assets/img/home/specialist.jpg); }
    .hero.specialist:before {
      background-color: rgba(198, 16, 201, 0.1); }
  .hero .logo {
    max-width: 100px;
    width: 30%;
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    border: solid 15px #ffffff; }
  .hero h1 {
    max-width: 250px;
    position: absolute;
    right: 0;
    bottom: -33px;
    left: 0;
    margin: auto;
    padding: 12px;
    font-size: 5vmax;
    color: #ffffff;
    text-align: center; }
    .hero h1.hair {
      background-color: #ffce1a; }
    .hero h1.beauty {
      background-color: #40ff95; }
    .hero h1.specialist {
      background-color: #c610c9; }



