.back {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  font-weight: bold;
  font-size: 30px;
  margin: 5px; }
  .back.hair i {
    background-color: transparent;
    color: #ffce1a; }
    .back.hair i:hover {
      background-color: #ffce1a;
      color: #ffffff; }
  .back.beauty i {
    background-color: transparent;
    color: #40ff95; }
    .back.beauty i:hover {
      background-color: #40ff95;
      color: #ffffff; }
  .back.specialist i {
    background-color: transparent;
    color: #c610c9; }
    .back.specialist i:hover {
      background-color: #c610c9;
      color: #ffffff; }
  .back i {
    width: 100%;
    height: 100%;
    position: relative;
    color: #000000; }
    .back i:hover {
      background-color: #000000;
      color: #ffffff; }
    .back i:after {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      text-align: center;
      line-height: 40px; }



